body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  font-family: 'Gothic A1', sans-serif;
  font-weight: 300;
}

.button{
  background-color: #D9D9D9; /* Green */
  border: none;
  color: #020D13;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
  margin: 30px
}

p{
  margin: 0px
}

h1{
  font-size: calc(120px + 2vmin);
  margin: 30px
}

.App-header {
  background-color: #020D13;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(30px + 2vmin);
}

.App-body {
  margin-top: 0;
  font-size: calc(13px + 2vmin);
  text-align: left;
  margin: 30px;
  padding-top: 0px; 
  padding-bottom: 0px; 
}

.App-bottom {
  font-size: calc(1px + 1vmin);
  text-align: center;
  position: absolute;
  bottom: 10px
}

.App-link {
  color: #61dafb;
}



.h1{
  font-size: calc(100px + 2vmin);
}

.FilmsWatched-Header {
  background-color: #CE62B6;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(30px + 2vmin);
  color: white;
  
}

.title{
  font-size: calc(60px + 2vmin);
}

.FilmsWatchedBody {
  margin-top: 0;
  font-size: calc(13px + 2vmin);
  text-align: left;
  margin: 0px;
}

.App-link {
  color: #61dafb;
}


